




















































































import Vue from "vue";
import MainLayout from "../components/MainLayout.vue";
import ReviewFilters from "@/components/ReviewFilters.vue";
import { mapActions } from "vuex";
import moment from "moment";

export default Vue.extend({
  name: "ReviewerTracking",
  components: {
    MainLayout,
    ReviewFilters,
  },
  data: () => ({
    reviewers: [],
    search: "",
    filters: {},
  }),
  methods: {
    ...mapActions(["getReviewStatsByReviewer"]),
    navigateWithFilters(page: string, reviewer: string) {
      let path: string;
      let reviewers = [reviewer];
      let reviewTypes: string[] = [];
      switch (page) {
        case "performanceReport":
          path = `/reviewer-tracking/${reviewer}/performance-report`;
          reviewers = [];
          break;
        case "analyzed":
          path = "/reviews";
          reviewTypes = ["FAULTY", "SUCCESSFUL"];
          break;
        case "faulty":
          path = "/reviews";
          reviewTypes = ["FAULTY"];
          break;
        default:
          path = "/reviews";
          break;
      }

      this.$router.push({
        path: `/${this.$route.params.repoUrl}` + path,
        query: { ...this.$route.query, reviewTypes, reviewers },
      });
    },
    async listReviewers(filters: any) {
      this.filters = filters;
      this.reviewers = await this.getReviewStatsByReviewer({
        repoUrl: this.$route.params.repoUrl,
        filters,
      });
    },
  },
  computed: {
    headers: () => [
      {
        text: "Reviewer",
        align: "start",
        value: "_id",
      },
      { text: "Analyzed Review Count", value: "analyzedCount" },
      { text: "Faulty Review Count", value: "faultyCount" },
      { text: "Total Review Count", value: "totalCount" },
      { text: "Average Review Accuracy", value: "averageAccuracy" },
    ],
    analyzedCountTooltipText: () => `Analyzed review count is the number of reviews performed\n
                                     by a reviewer in the specified time period that have been\n
                                     analyzed to check what percentage of injected bugs they\n
                                     were able to catch during their review.`,
    faultyCountTooltipText: () => `Faulty review count is the number of analyzed reviews of\n
                                   a reviewer in the specified time period that caught a\n
                                   percentage of bugs that was below the minimum threshold\n
                                   set by the admin. E.g. if the threshold is 70% and a review\n
                                   caught 69% of bugs, it is considered faulty.`,
    totalCountTooltipText: () => `Total review count is the total number of reviews that were\n
                                  performed by a reviewer in the specified time period`,
    averageAccuracyTooltipText: () => `Average accuracy is the average percentage of bugs\n
                                       that a reviewer is able to catch in their reviews\n
                                       which were analyzed`,
  },
  mounted() {
    this.listReviewers({
      startDate: moment(new Date()).subtract(1, "months").format("YYYY-MM-DD"),
      endDate: moment(new Date()).format("YYYY-MM-DD"),
    });
  },
});
